import styled from "styled-components";

import {
	colors, marginsCss, spacingMap,
} from "../styleguide";

import { StyledToggleProps } from "./Toggle.types";

const transitionDuration = "0.4s";

export const ToggleWrapper = styled.div<StyledToggleProps>`
	display: flex;
	${marginsCss};
`;

export const StyledToggleButton = styled.button<StyledToggleProps>`
	position: relative;
	border: none;
	box-sizing: border-box;

	width: ${spacingMap.lg};
	height: 20px;
	background-color: ${({ isActive }) => isActive ? colors.midBlue : colors.lightGray};
	border-radius: 10px;

	transition-duration: ${transitionDuration};
	transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
	cursor: pointer;

	&:hover {
		background-color: ${({ isActive }) => isActive ? colors.midBlue : colors.darkGray};
	}

	&:focus {
		outline-offset: 0px;
		outline: 1px solid ${colors.midBlue};
		box-shadow: 0 0 0 4px ${colors.midBlue}40;
	}

	&:disabled {
		cursor: not-allowed;
		outline: ${({ isActive }) => isActive ? `1px solid ${colors.midBlue}80` : `1px solid ${colors.lightGray}`};
		background-color: ${({ isActive }) => isActive ? `${colors.midBlue}80` : colors.white};
	}
`;

export const InnerToggle = styled.div<StyledToggleProps>`
	width: 16px;
	height: 16px;
	border-radius: 8px;
	background-color: ${({ disabled, isActive }) => {
		if (disabled) {
			if (!isActive) {
				return colors.lightGray;
			}
			return colors.white;
		}
		return colors.white;
	}};

    ${({ disabled }) => !disabled && `box-shadow: 0 0 1px ${colors.black}52, 0 2px 8px ${colors.black}1A`};
	box-sizing: border-box;

	position: absolute;
	top: 2px;
	left: 2px;
	right: 2px;
	bottom: 2px;

	display: flex;
	justify-content: center;
	align-items: center;

	transform: translateX(${({ isActive }) => isActive ? "12px" : "0"});
	transition-duration: ${transitionDuration};
	transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);

`;

